@import '~semantic-ui-css/semantic.min.css';

@mixin textStyle {
  .block__title {
    font-size: 2em;
  }
  .block__text {
    font-size: 1.33em;
  }
}
@mixin blockPadding {
  padding: 8em 0em;
}

@media print {
  .menu,
  #projects,
  #footer {
    display: none !important;
  }
  #about {
    padding-top: 15px !important;
    padding-bottom: 1em !important;
  }
  #exp {
    padding-top: 15px !important;
    padding-bottom: 15px !important;
  }
  .ui.inverted.segment.heading-image {
    height: 50px !important;
    background-image: none !important;
    background-color: white !important;
    &.desktop {
      min-height: 175px !important;
    }
  }
  .projects-anchor {
    display: none !important;
  }
  .ui.inverted.header {
    margin-top: 0em !important;
    font-size: 2em !important;
    color: black;
  }
  .ui.inverted.hompage__heading {
    &-sub__header {
      font-size: 1.5em !important;
      margin-top: 3em;
      font-weight: normal;
    }
  }
  .contacts {
    margin-top: 140px !important;
  }
}
@media screen {
  .project-page-link {
    display: none !important;
  }
}

.ui.inverted.segment.heading-image {
  background-image: linear-gradient(#00000091, #00000091),
    url(./images/code.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 100vw;
  &.desktop {
    min-height: 700px;
    padding: 1em 0em;
  }
  &.mobile {
    min-height: 350px;
    padding: 1em 0em;
  }
}

.grid .row .column.project-card {
  padding-top: 5em;
  padding-bottom: 5em;
  display: flex;
  justify-content: center;
}
#exp {
  @include blockPadding;
  @include textStyle;
  .courses__title {
    font-size: 1.5em;
    margin-bottom: 5px;
  }
  .courses__data {
    margin: 0.5em 0;
  }
  .empoyment__title {
    font-size: 1.5em;
  }
}
#projects{
  padding: 0em;
  .projects__header{
    font-size: 2em;
    padding: 2em 0;
  }
}
#about {
  @include blockPadding;
  @include textStyle;
  .contacts {
    margin: 3em 0em;
    text-transform: uppercase;
  }
}
.ui.inverted.hompage__heading {
  &-header {
    font-size: 4em;
    margin-top: 3em;
    font-weight: normal;
    &-mobile {
      font-size: 2em;
      margin-top: 1.5em;
      font-weight: normal;
    }
  }
  &-sub__header {
    font-size: 1.7em;
    margin-top: 1.5em;
    font-weight: normal;
    &-mobile {
      font-size: 1.5em;
      margin-top: 0.5em;
      font-weight: normal;
    }
  }
}
